     text-align: center;\n        text-decoration: none;\n        user-select: none;\n        -webkit-user-select: none;\n        width: 100%;\n        height: 90%;\n        align-self: center;\n\n        &:disabled {\n            pointer-events: none;\n        }\n\n        &:hover {\n            color: #fff;\n            background-color: #1a1a1a;\n            box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;\n            border-radius: 15px;\n        }\n\n        &:active {\n            box-shadow: none;\n        }\n    }\n\n    .search {\n        width: 50%;\n        height: fit-content;\n        input {\n            min-width: 50%;\n            min-height: 3rem;\n            font-size: 2rem;\n            padding: 1rem;\n            border-radius: 1rem;\n            border-color: #1a1a1a;\n            text-align: center;\n        }\n        button {\n            width: 15%;\n        }\n    }\n\n    .units {\n        width: fit-content;\n        height: fit-content;\n    }\n}\n#content {\n    height: 80%;\n    width: 80vw;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-self: center;\n    gap: 1rem;\n    padding: 3%;\n\n    #general_desc{\n        @include kaisei-harunoumi(); \n        font-size: 2.5rem;\n        text-align: center;\n    }\n    #infos{\n        display: flex;\n        gap: 10vw;\n        align-items: center;\n        margin: 1rem;\n        overflow: scroll;\n        height: 60vh;\n        .info {\n            display: flex;\n            align-items: center;\n            flex-direction: column;\n            min-width: 20vw;\n            min-height: 30vh;\n            gap: 1rem;\n            @include kaisei-harunoumi();\n            .temp_min_max{\n                display: grid;\n                width: 20vw;\n                text-align: center;\n                justify-content: center;\n                grid-template-columns: 50% 50%;\n                grid-template-rows: auto;\n                grid-template-areas: \n                \".temp_minmax .temp_minmax\";\n                .temp_minmax{\n                    justify-self: center;  \n                    margin: 1rem;\n                }\n            }\n    \n            img{\n                width: 30%;\n            }\n            h1{\n                font-size: 2rem;\n            }\n            h2{\n                font-size: 1.4rem;\n            }\n            p{\n                font-size: 1.2rem;\n            }\n        }\n    }\n}\n\ndialog#loading {\n    width: fit-content;\n    height: fit-content;\n    padding: 2rem;\n    transform: translate(-50%, -50%);\n    color: whitesmoke;\n    background-color: #1e1e1e;\n    border-radius: 2.375rem;\n    border: none;\n    position: fixed;\n    align-items: center;\n    top: 50%;\n    left: 50%;\n    h1 {\n        font-size: 2rem;\n        @include kaisei-harunoumi();\n    }\n}\n\n// the following is the tool class\n.click {\n    background-color: black;\n    color: whitesmoke;\n}\n"],"sourceRoot":""}]);
